<template>
  <div>
    <div>
    <table class="table table-bordered w-50">
      <tr>
        <th>번호</th>
        <td>{{ item.qnaNo }}</td>
      </tr>
      <tr>
        <th>글쓴이</th>
        <td>{{ item.qnaUserid }}</td>
      </tr>
      <tr>
        <th>제목</th>
        <td>{{ item.qnaTitle }}</td>
      </tr>
      <tr>
        <th>날짜</th>
        <td>{{ getFormatDate(item.qnaDatetime) }}</td>
      </tr>
      <tr>
        <td colspan="2">
          {{ item.qnaContent }}
        </td>
      </tr>
    </table>

    <br>
    
  </div>
    
    <div v-if="user == 'admin'">
        <br>
      <div class="text-center">

        <div v-if="user=='admin'">
        <div v-if="view">
          <textarea class="form-control" v-model="replyContent" ></textarea>
          <button class="btn btn-primary" v-on:click="replyWrite">등록</button>
        </div>
        <div v-if ="item.replyContent=='' || item.replyContent==null"> 
          <button v-if="!view" class="btn btn-primary" v-on:click="reply">답변</button>
          
        <router-link to="/list">
          <button class="btn btn-primary">목록</button>
          </router-link>
        </div>
        
        <div v-else>
          <div>
            <table class="table table-bordered w-50">
      <tr>
        <th>글쓴이</th>
        <td>{{ item.replyUserid }}</td>
      </tr>
      <tr>
        <th>날짜</th>
        <td>{{ getFormatDate(item.replyDatetime) }}</td>
      </tr>
      <tr>
        <td colspan="2">
          <textarea class="form-control" :disabled="!modiview" v-model="item.replyContent"></textarea> 
          <button v-if="modiview" class="btn btn-primary" @click="replyModi">등록</button>
        </td>
      </tr>
    </table>

          </div>
          <div v-if="!modiview">
          <button class="btn btn-warning" v-on:click="remodi">답변 수정</button>
          <button class="btn btn-danger" @click="replyDel">답변 삭제</button>
          </div>
          <br>
          <router-link to="/list">
          <button class="btn btn-primary">목록</button>
          </router-link>
        </div>
          
        <br>
        
      </div>
    
    </div>
    </div>
    <div v-else-if="item.qnaUserid==user">
      <br>
      <div class="text-center">
        <router-link to="/list"
          ><button class="btn btn-primary">목록</button></router-link
        >
        <router-link :to="'/update?qnaNo=' + item.qnaNo"
          ><button class="btn btn-warning">수정</button></router-link
        >
        <router-link :to="'/delete?qnaNo=' + item.qnaNo"
          ><button class="btn btn-danger">삭제</button></router-link
        >
      </div>
    </div>
    <div v-else>
      <router-link to="/list"><button class="btn btn-primary">목록</button></router-link>
    </div>

  </div>
</template>

<script>
import http from '@/util/http-common';
// import Detail from '@/components/Detail.vue';
import moment from 'moment';
export default {
  name: 'read',
  // components: {
  //   Detail,
  // },
  data: function() {
    return {
      item: {},
      user: '',
      replyContent: '',
      view: false,
      modiview : false,
    };
  },
  created() {
    http.get(`/qna/${this.$route.query.no}`).then(({ data }) => {
      
      this.item = data;
      //alert(this.$route.query.qnaNo);
    });
    http.get(`/qna/getUser`).then(({data})=>{
      this.user = data;
    })
    .catch(() =>{
      alert('에러');
    });
    
  },
  methods: {
    reply: function (){ // eslint-disable-line no-unused-vars
      this.view=!this.view;
    },
    replyWrite() {
      let content = {
        replyContent: this.$data.replyContent,
        replyUserid: "admin",
        qnaNo: this.$route.query.no,
      }
        http.post("/qna/r", content).then(()=>{
          this.$router.push({
            name: "list",
          });
      });
    },
    remodi(){
      this.modiview = !this.modiview;
    },
    replyModi(){
      let item = this.$data.item;
      http.put(`/qna/r/${this.$data.item.qnaNo}`, item).then(()=>{
          this.$router.push({
            name: "list",
          });
      });
    },
    replyDel(){
      http.delete(`/qna/r/${this.$data.item.qnaNo}`).then(()=>{
          this.$router.push({
            name: "list",
          });
      });
    },
    getFormatDate(qnaDatetime) {
      return moment(new Date(qnaDatetime)).format('YYYY.MM.DD HH:mm:ss');
    },
  },
};
</script>

<style></style>
